.main_footer {
  width: 100%;
  border-top: rem(50px) solid #000000;
  position: relative;
  .footer_content {
    width: 100%;
    position: relative;
    background-color: #1f1f1f;
    padding: rem(124px) 0 rem(153px);
    .container {
      display: flex;
      justify-content: space-between;
      .footer_text {
        max-width: rem(385px);
        width: 100%;
        color: #ffffff;
        h2 {
          text-transform: uppercase;
          font-size: rem(40px);
          font-weight: 700;
          color: #ffffff;
        }
        p {
          margin-top: rem(40px);
          font-family: 'Lato';
          font-size: rem(18px);
          font-weight: 400;
          line-height: 1.5;
        }
        a {
          display: block;
          margin-top: rem(40px);
          font-size: rem(30px);
          font-weight: 700;
        }
      }
      .feedback {
        max-width: rem(456px);
        width: 100%;
        position: relative;
        input {
          width: 100%;
          height: rem(70px);
          background-color: #000000;
          color: #ffffff;
          border: none;
          margin-top: rem(30px);
          font-family: 'Lato';
          font-size: rem(20px);
          font-weight: 400;
          text-align: center;
          transition: .4s;
          &:focus {
            background-color: #161616;
          }
          &:first-child {
            margin-top: 0;
          }

        }
        .btn {
          margin-top: rem(30px);
          background-color: #1f1f1f;
          width: 100%;
          color: $yellow-color;
          &:hover {
            background-color: $yellow-color;
            color: #1f1f1f;
          }
        }
      }
    }
  }
  .copywriter {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: rem(40px);
    color: #ffffff;
    font-family: "Myriad Pro";
    font-size: rem(20px);
    font-weight: 400;
    background-color: #000000;
    span {
      margin-right: rem(10px);
    }
  }
}


