body {
  font-family: 'Proxima Nova';
  font-weight: 400;
  font-style: normal;
  font-size: rem(18px);
}

/*main section*/
.main_section {
  width: 100%;
  position: relative;
  overflow: hidden;
  &::before {
    @include before_fig(100%, 100%, rgba(6, 6, 6, 0.85));
    top: 0;
    left: 0;
    z-index: 1;
  }
  .parallax {
    position: absolute;
  }
}

//BTN
.btn {
  border: rem(3px) solid $yellow-color;
  font-size: rem(16px);
  font-weight: 700;
  text-transform: uppercase;
  width: rem(390px);
  height: rem(60px);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-top: rem(35px);
  transition: .4s;
  &:hover {
    background-color: $yellow-color;
    color: #ffffff;
  }
}

/*-----------------*/
.container {
  max-width: rem(1170px);
  width: 100%;
  margin: 0 auto;
  position: relative;

}

/*social*/
.social {
  display: flex;

  a {
    font-size: rem(30px);
    color: #ffcc01;
    opacity: 0.15;
    transition: 0.3s;

    &:hover {
      opacity: 1;
      transform: scale(1.2);
      transition: 0.3s;
    }
  }
}

/*---------------*/
.main_title {
  position: relative;
  color: #ffcc01;
  font-size: 60px;
  font-weight: 700;
  text-transform: uppercase;
  width: 100%;
  span {
    font-size: rem(126px);
    opacity: 0.2;
    position: absolute;
  }
}

.main_line {
  width: 70%;
  height: rem(127px);
  background-color: #fff;
  position: absolute;
  top: -1%;
  right: 0;
  z-index: 6;
  display: flex;
  align-items: flex-end;

  &:nth-child(2) {
    bottom: rem(-6px);
    left: 0;
    top: auto;
    right: auto;
    transform: rotate(180deg);
  }
  &::before {
    content: "";
    position: absolute;
    left: rem(-62px);
    display: block;
    width: 0;
    height: 0;
    border-bottom: rem(127px) solid #ffffff;
    border-left: rem(63px) solid transparent;
  }
}

//GYM FOOTER
.gym_footer {
  display: flex;
  justify-content: space-between;
  margin: rem(60px) 0 rem(60px);
  .main_nav {
    a {
      color: #989898;
      font-size: rem(14px);
      font-weight: 700;
      margin-left: rem(40px);
      text-transform: uppercase;
      transition: .4s;
      position: relative;
      &:hover {
        color: $yellow-color;
      }
      &:first-child {
        margin-left: 0;
        &::before {
          @include before_fig(rem(4px), rem(4px), $yellow-color);
          right: -37%;
          top: 45%;
        }
      }
      &:nth-child(3) {
        &::before {
          @include before_fig(rem(4px), rem(4px), $yellow-color);
          left: rem(-23px);
          top: 45%;
        }
      }
    }
  }
}

//END GYM FOOTER
/*slider btn*/

.slider_btn {
  border: none;
  position: absolute;
  width: rem(236px);
  height: rem(126px);
  background-color: rgba(255, 204, 1, .16);
  color: $yellow-color;
  transition: .4s;
  cursor: pointer;
  &:hover {
    background-color: $yellow-color;
    color: #ffffff;
  }
  .icon-left-open, .icon-right-open {
    font-size: rem(50px);
    position: relative;

  }
}

.left_services {
  bottom: 20%;
  left: 0;
}

.right_services {
  top: 25%;
  right: 27%;
  z-index: 6;

}

.trainers_section {
  position: relative;
  width: 100%;
  overflow: hidden;
  &::before {
    @include before_fig(100%, 100%, rgba(6, 6, 6, 0.92));
    top: 0;
    left: 0;
    z-index: 1;
  }
  .parallax {
    position: absolute;
  }
  .container {
    position: relative;
    z-index: 2;
  }
}

.container_news {
  max-width: rem(1560px);
  width: 100%;
  position: relative;
  z-index: 1;
  margin: 0 auto;
}

/*trainers elem*/
.trainers_elem {
  display: flex;
  flex-direction: column;
  position: relative;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  p {
    text-align: center;
    color: #ffffff;
    font-size: rem(17px);
    margin-top: rem(20px);
  }
  .name_trainers {
    text-transform: uppercase;
  }
  .position_trainers {
    font-family: 'lato';
    font-weight: 400;
  }
  &:nth-child(4n+1) {
    top: rem(15px);
  }
  &:nth-child(4n+2) {
    bottom: rem(15px);
  }
  &:nth-child(4n+3) {
    top: rem(30px);
  }
}

//NEWS ELEM
.news_elem {
  display: flex;
  width: 100%;
  position: relative;
  .elem_img {
    max-width: rem(292px);
    width: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;

    }
  }
  .news_text {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    max-width: rem(292px);
    width: 100%;
    padding: rem(40px) rem(20px) 0 rem(40px);
    .content_title {
      color: #1f1f1f;
      font-size: rem(17px);
      font-weight: 700;
      text-transform: uppercase;
    }
    .content_date {
      margin-top: rem(15px);
      color: #1f1f1f;
      font-size: rem(17px);
      font-weight: 700;
    }
    .text_item {
      text-indent: rem(20px);
      margin-top: rem(20px);
      color: #1f1f1f;
      font-family: 'Lato';
      font-size: rem(17px);
      font-weight: 400;
      line-height: 1.41;
      max-height: rem(70px);
      overflow: hidden;
    }
    .btn {
      width: rem(165px);
    }
  }

}




